import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useLocation } from 'react-router-dom';

import TitleBar from '../components/TitleBar';

import styles from './drivingRegisterPage.module.css';
import { SearchBar } from '../components/ExplorePage';

function DrivingRegisterPage() {
  const { search } = useLocation();
  const source = new URLSearchParams(search).get('s') || 0;

  const [frontsideName, setFrontsideName] = useState(null);
  const [frontsideFile, setFrontsideFile] = useState(null);
  const [backsideName, setBacksideName] = useState(null);
  const [backsideFile, setBacksideFile] = useState(null);
  const [portraitName, setPortraitName] = useState(null);
  const [portraitFile, setPortraitFile] = useState(null);
  const [receiptName, setReceiptName] = useState(null);
  const [receiptFile, setReceiptFile] = useState(null);
  const [paymentMethod, setPaymentMethod] = useState(1);
  const [isPaid, setIsPaid] = useState(1);
  const [drivingType, setDrivingType] = useState(0);
  const [date, setDate] = useState(0);
  const [dateList, setDateList] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [searchValue, setSearchValue] = useState('');
  const [searchData, setSearchData] = useState(null);

  const imageExtensions = [
    'image/jpeg',
    'image/png',
    'image/svg+xml',
    'image/webp',
  ];

  useEffect(async () => {
    try {
      const response = await axios.get('/api/driving/date?formVisible=true');
      let data = response.data.data;

      if (data.length > 0) {
        data = data.map((child) => {
          return {
            ...child,
            date: new Date(child.date),
          };
        });

        setDateList(data);
      } else {
        alert('Cha c danh sch ngy thi mi');
      }
    } catch (e) {
      alert('Li: ' + e);
    }
  }, []);

  const handleSubmitButton = (e) => {
    setIsLoading(true);
    console.log(dateList[date].date.toLocaleDateString());
    if (!frontsideFile) {
      setIsLoading(false);
      return alert('Li: Vui lng ti ln mt trc cmnd/cccd');
    }
    if (!backsideFile) {
      setIsLoading(false);
      return alert('Li: Vui lng ti ln mt sau cmnd/cccd');
    }
    if (!portraitFile) {
      setIsLoading(false);
      return alert('Li: Vui lng ti ln nh chn dung ca bn');
    }

    const fullname = document.getElementById('formName').value;
    const tel = document.getElementById('formTel').value;
    const zalo = document.getElementById('formZalo').value;
    const feedback = document.getElementById('formFeedback').value;
    let paidState = isPaid === 0;

    if (!fullname || !tel || !zalo) {
      return alert(
        'Vui lng nhp y : h tn, s in thoi v s in thoi zalo'
      );
    }

    const formData = new FormData();

    formData.append('name', fullname);
    formData.append('tel', tel);
    formData.append('zalo', zalo);
    formData.append('frontside', frontsideFile);
    formData.append('backside', backsideFile);
    formData.append('portrait', portraitFile);
    formData.append('receipt', receiptFile);
    formData.append('isPaid', paidState);
    formData.append('paymentMethod', paymentMethod);
    formData.append('feedback', feedback);
    formData.append('drivingType', drivingType);
    formData.append('source', source);

    if (drivingType === 0) {
      formData.append('date', dateList[date].date.getTime());
    }

    axios({
      method: 'post',
      url: '/api/driving/add',
      data: formData,
      headers: { 'Content-Type': 'multipart/form-data' },
    })
      .then((res) => {
        if (res.status === 200) {
          alert(
            'ng k thnh cng. Trung tm s lin h vi bn trong thi gian sm nht. Nu bn cn h tr thm, vui lng lin h zalo  c x l.'
          );
          setIsLoading(false);
        } else {
          alert('Li: ' + res.data.message);
          setIsLoading(false);
        }
      })
      .catch((error) => {
        console.log(error);
        alert('Li: ' + error);
        setIsLoading(false);
      });
  };

  const handleIsPaidChange = (value) => {
    setIsPaid(value);
  };

  const handlePaymentMethodChange = (value) => {
    setPaymentMethod(value);
  };

  const handleDrivingTypeChange = (value) => {
    setDrivingType(value);
  };

  const handleDateChange = (value) => {
    setDate(value);
  };

  const uploadFrontside = (e) => {
    if (imageExtensions.includes(e.target.files[0].type)) {
      setFrontsideFile(e.target.files[0]);
      setFrontsideName(e.target.files[0].name);
      console.log(e.target.files[0]);
    } else {
      setFrontsideName('Li: Tp ti ln khng phi l tp hnh nh');
    }
  };
  const uploadBackside = (e) => {
    if (imageExtensions.includes(e.target.files[0].type)) {
      setBacksideFile(e.target.files[0]);
      setBacksideName(e.target.files[0].name);
    } else {
      setBacksideName('Li: Tp ti ln khng phi l tp hnh nh');
    }
  };
  const uploadPortrait = (e) => {
    if (imageExtensions.includes(e.target.files[0].type)) {
      setPortraitFile(e.target.files[0]);
      setPortraitName(e.target.files[0].name);
    } else {
      setPortraitName('Li: Tp ti ln khng phi l tp hnh nh');
    }
  };
  const uploadReceipt = (e) => {
    if (imageExtensions.includes(e.target.files[0].type)) {
      setReceiptFile(e.target.files[0]);
      setReceiptName(e.target.files[0].name);
    } else {
      setReceiptName('Li: Tp ti ln khng phi l tp hnh nh');
    }
  };

  const handleSearchChange = (e) => {
    console.log(searchValue);
    setSearchValue(e.target.value);
  };

  const handleSearchPress = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      axios
        .get('/api/driving/search', { params: { tel: searchValue } })
        .then((res) => {
          const data = res.data.data;

          if (data.length === 0) {
            alert('Khng tm thy h s khp vi s in thoi ' + searchValue);
          } else {
            setSearchData(data);
          }
        })
        .catch((e) => {
          console.log(e);
          alert('Khng tm thy h s khp vi s in thoi ' + searchValue);
        });
    }
  };

  return (
    <div className={styles.drivingRegisterContainer}>
      <TitleBar title='ng k d thi' navigation='/driving-test' />
      <SearchBar
        placeholder={'Tra cu trng thi h s'}
        focusText={'Nhp s in thoi v nhn Enter'}
        onChange={handleSearchChange}
        onKeyPress={handleSearchPress}
        value={searchValue}
      />
      {searchData &&
        searchData.map((child) => {
          const date = new Date(child.date);
          const processDate = new Date(date);
          processDate.setDate(date.getDate() - 14);
          let state = '';

          if (child.processState == 0) {
            state = 'ang ch x l';
          } else if (child.processState == 1) {
            state = 'ang ch cp nht';
          } else if (child.processState == 2) {
            state = 'ang ch thanh ton';
          } else if (child.processState == 3) {
            state = ' hon tt h s';
          } else if (child.processState == 4) {
            state = ' hy h s';
          }

          return (
            <div className={styles.orderContainer}>
              <p>H tn: {child.name}</p>
              <p>
                Ngy d thi:
                {` ${date.getDate()}/${
                  date.getMonth() + 1
                }/${date.getFullYear()}`}
              </p>
              <p>Trng thi: {state}</p>
              <p>
                H s ca bn s c x l vo ngy:
                {` ${processDate.getDate()}/${
                  processDate.getMonth() + 1
                }/${processDate.getFullYear()}`}
              </p>
              <p>
                Vui lng lin h{' '}
                <a
                  href='https://zalo.me/0383270434'
                  target='_blank'
                  rel='noopener noreferrer'
                >
                  0383.270.434
                </a>{' '}
                 c h tr trong trng hp h s ca bn cha c x l.
              </p>
            </div>
          );
        })}
      <form className={styles.drivingFormContainer}>
        <p style={{ margin: 0 }}>
          Xem hng dn ng k d thi{' '}
          <a
            href='/driving-instruction'
            target='_blank'
            rel='noopener noreferrer'
          >
            ti y.
          </a>
        </p>
        <ul style={{ listStyle: 'none', padding: '0' }}>
          <li>
            Thng tin chuyn khon:
            <br />- Ch ti khon: Nguyn Ngc Hun
            <br />- Ngn hng: MB Bank (Ngn hng Qun i)
            <br />- S ti khon: 0877876877
            <br />- Ni dung: H tn_ST_Bang A1_Ngy d thi
            <br />- S tin: 590.000 ng i vi bng A1, 2.000.000 ng i
            vi bng A2
            <br />- Gi li nh chp bin lai trong form ng k nu  chuyn
            khon.
            <br />- Gi li nh chp bin lai ti Zalo:{' '}
            <a
              href='https://zalo.me/0383270434'
              target='_blank'
              rel='noopener noreferrer'
            >
              0383.270.434
            </a>{' '}
            nu ng sau khi ng k.
          </li>

          <li>Hon thnh l ph thi trc ngy d thi 14 ngy.</li>
        </ul>
        <p style={{ margin: 0, color: ' #ff0000 ' }}>* bt buc</p>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Tn ca bn*</label>
          <input
            className={styles.formInput}
            id='formName'
            type='text'
            placeholder='Nhp h tn y , c du'
            required
          />
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>S in thoi lin h*</label>
          <input
            className={styles.formInput}
            id='formTel'
            type='text'
            placeholder='Nhp s in thoi ca bn'
            required
          />
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>S in thoi Zalo*</label>
          <input
            className={styles.formInput}
            id='formZalo'
            type='text'
            placeholder='Nhp s in thoi Zalo ca bn'
          />
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Mt trc CMND/CCCD*</label>
          <label className={styles.formUploadButton}>
            <input
              className={styles.formInput}
              type='file'
              accept='image/*'
              id='frontside'
              name='frontside'
              onChange={uploadFrontside}
              required
            />
            Ti tp ln
          </label>
          {frontsideName ? (
            <p className={styles.formFilename}>{frontsideName}</p>
          ) : null}
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Mt sau CMND/CCCD*</label>
          <label className={styles.formUploadButton}>
            <input
              className={styles.formInput}
              type='file'
              accept='image/*'
              id='backside'
              name='backside'
              onChange={uploadBackside}
              required
            />
            Ti tp ln
          </label>
          {backsideName ? (
            <p className={styles.formFilename}>{backsideName}</p>
          ) : null}
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>
            nh chp chn dung* (khng chp nh th, xem nh mu trong hng dn{' '}
            <a
              href='/driving-instruction'
              target='_blank'
              rel='noopener noreferrer'
            >
              ti y
            </a>
            )
            <br />
            Xem video hng dn to nh nn xanh ti{' '}
            <a
              href='https://youtu.be/56xW53bxNLg'
              target='_blank'
              rel='noopener noreferrer'
            >
              https://youtu.be/56xW53bxNLg
            </a>
          </label>
          <label className={styles.formUploadButton}>
            <input
              className={styles.formInput}
              type='file'
              accept='image/*'
              id='portrait'
              name='portrait'
              onChange={uploadPortrait}
              required
            />
            Ti tp ln
          </label>
          {portraitName ? (
            <p className={styles.formFilename}>{portraitName}</p>
          ) : null}
        </div>

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Chn loi bng li*</label>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type='radio'
              onChange={() => handleDrivingTypeChange(0)}
              checked={drivingType === 0}
            />
            <p onClick={() => handleDrivingTypeChange(0)}>
              Bng A1 (M t 2 bnh c dung tch xi lanh t 50cc cho n di
              175cc, l ph thi 590.000 ng, chn ngy thi  mc bn di)
            </p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type='radio'
              onChange={() => handleDrivingTypeChange(1)}
              checked={drivingType === 1}
            />
            <p onClick={() => handleDrivingTypeChange(1)}>
              Bng A2 (M t 2 bnh khng gii hn dung tch xi lanh, l ph thi
              2.000.000 ng, trung tm lin h hng dn qua in thoi)
            </p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type='radio'
              onChange={() => handleDrivingTypeChange(2)}
              checked={drivingType === 2}
            />
            <p onClick={() => handleDrivingTypeChange(2)}>
              Bng B2 (Bng li xe  t, trung tm lin h hng dn qua in
              thoi)
            </p>
          </div>
        </div>

        {drivingType === 0 ? (
          <div className={styles.formGroup}>
            <label className={styles.formLabel}>Chn ngy thi*</label>

            {dateList.map((child, index) => {
              return (
                <div className={styles.selectContainer}>
                  <input
                    className={styles.formInput}
                    type='radio'
                    onChange={() => handleDateChange(index)}
                    checked={date === index}
                  />
                  <p onClick={() => handleDateChange(index)}>
                    {child.description || child.date.toLocaleDateString()}
                  </p>
                </div>
              );
            })}
          </div>
        ) : null}

        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Hnh thc ng l ph*</label>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type='radio'
              onChange={() => handlePaymentMethodChange(0)}
              checked={paymentMethod === 0}
            />
            <p onClick={() => handlePaymentMethodChange(0)}>
              ng trc tip ti nh khch HQG
            </p>
          </div>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type='radio'
              onChange={() => handlePaymentMethodChange(1)}
              checked={paymentMethod === 1}
            />
            <p onClick={() => handlePaymentMethodChange(1)}>
              Chuyn khon ngn hng
            </p>
          </div>
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Bn  ng l ph cha?*</label>
          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type='radio'
              onChange={() => handleIsPaidChange(0)}
              checked={isPaid == 0}
            />
            <p onClick={() => handleIsPaidChange(0)}> thanh ton</p>
          </div>

          <div className={styles.selectContainer}>
            <input
              className={styles.formInput}
              type='radio'
              onChange={() => handleIsPaidChange(1)}
              checked={isPaid == 1}
            />
            <p onClick={() => handleIsPaidChange(1)}>Mnh s ng l ph sau</p>
          </div>
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>
            nh chp bin lai (nu  ng l ph)
          </label>
          <label className={styles.formUploadButton}>
            <input
              className={styles.formInput}
              type='file'
              accept='image/*'
              id='receipt'
              name='receipt'
              onChange={uploadReceipt}
            />
            Ti tp ln
          </label>

          {receiptName ? (
            <p className={styles.formFilename}>{receiptName}</p>
          ) : null}
        </div>
        <div className={styles.formGroup}>
          <label className={styles.formLabel}>Thc mc/Gp </label>
          <textarea className={styles.formFeedbackInput} id='formFeedback' />
        </div>

        {isLoading ? (
          <>
            <p>
              H thng ang x l, vui lng ch trong t nht 15 giy {'<3'}
            </p>
            <p className={styles.formSubmitButton}>ang ng k...</p>
          </>
        ) : (
          <button
            type='button'
            onClick={handleSubmitButton}
            className={styles.formSubmitButton}
          >
            ng k
          </button>
        )}
        <p style={{ margin: '1rem 0' }}>
          Trong qu trnh ng k, nu xy ra li h thng, vui lng chp mn
          hnh li gi v Zalo:{' '}
          <a
            href="href='https://zalo.me/0797324886"
            target='_blank'
            rel='noopener noreferrer'
          >
            0797324886
          </a>{' '}
           c h tr nhanh nht. Xin cm n.
        </p>
      </form>
    </div>
  );
}

export default DrivingRegisterPage;
