import React, { useEffect, useState } from 'react';
// import { useParams } from 'react-router-dom';
import axios from 'axios';
import TitleBar from '../components/TitleBar';

import { MdOutlineContentCopy } from 'react-icons/md';
import styles from './jobDetailPage.module.css';

function JobDetailPage(props) {
  const id = new URLSearchParams(props.location.search).get('id');
  const [data, setData] = useState(null);
  const [telCopied, setTelCopied] = useState(false);
  const [zaloCopied, setZaloCopied] = useState(false);

  useEffect(() => {
    axios
      .get(`/api/job/${id}`)
      .then((res) => {
        if (res.data.data) {
          setData(res.data.data);
        }
      })
      .catch((error) =>
        alert(
          'Xin li, khng tm thy cng vic ny hoc cng vic  b g b!'
        )
      );
  }, []);

  const handleTelCopy = () => {
    navigator.clipboard.writeText(data.tel);
    setTelCopied(true);
  };

  const handleZaloCopy = () => {
    navigator.clipboard.writeText(data.zalo);
    setZaloCopied(true);
  };

  return (
    <>
      <TitleBar title='Thng tin vic lm' navigation='/jobs' />
      <div className={styles.container}>
        <div className={styles.imageContainer}>
          <img src={data && data.banner} />
        </div>
        <div className={styles.infoContainer}>
          <p>
            <strong>Tn cng vic:</strong> {data && data.title}
          </p>
          <p>
            <strong>a ch:</strong> {data && data.address}
          </p>
          <p>
            <strong>Mc lng:</strong> {data && data.price}
          </p>
          <p>
            <strong>Thi gian: </strong>
            {data && data.time}
          </p>
          <p>
            <strong>M t chi tit:</strong> {data && data.description}
          </p>
          <p>
            <span style={{ color: 'var(--primary)', fontWeight: 'bold' }}>
              Di ng:{' '}
            </span>
            {data && data.tel}
            <span onClick={handleTelCopy} className={styles.copyButton}>
              {telCopied ? ' chp' : <MdOutlineContentCopy />}
            </span>
          </p>

          <p>
            <span style={{ color: 'var(--primary)', fontWeight: 'bold' }}>
              Zalo:{' '}
            </span>
            {data && data.zalo ? (
              <>
                {data.zalo}
                <span onClick={handleZaloCopy} className={styles.copyButton}>
                  {zaloCopied ? ' chp' : <MdOutlineContentCopy />}
                </span>
              </>
            ) : (
              'Khng c thng tin'
            )}
          </p>
        </div>
      </div>
    </>
  );
}

export default JobDetailPage;
