import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import axios from 'axios';

//redux
import { useSelector, useDispatch } from 'react-redux';
import { selectUser, logoutUser } from '../store/userSlice';

import styles from './accountPage.module.css';

//component
import {
  AccountInfo,
  AccountHistory,
  Tool,
} from '../components/AccountPage/index';
import AccountItem from '../components/AccountPage/AccountItem';
import MainLayout from '../layouts/MainLayout';

function AccountPage() {
  const history = useHistory();
  const dispatch = useDispatch();
  const user = useSelector(selectUser);

  const handleLoginClick = () => {
    history.push('/login');
  };

  const handleSignUpClick = () => {
    history.push('/login');
  };

  // const handleUpdateInfo = () => {
  //   history.push('/profile');
  // };
  const handleLogout = () => {
    const refreshToken = localStorage.getItem('user-jwt-rftk');
    axios
      .post('/api/user/logout', { refreshToken })
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        console.log(error);
      });
    localStorage.removeItem('user-info');
    localStorage.removeItem('user-jwt-tk');
    localStorage.removeItem('user-jwt-rftk');
    dispatch(logoutUser());
  };

  return (
    <MainLayout>
      <div className={styles.accountPageContainer}>
        {user.isLoggedIn ? (
          <>
            <AccountInfo />
            {/* <AccountHistory />
          <Tool handle={handleUpdateInfo} title='Cp nht thng tin' />
          <Tool handle={handleUpdateInfo} title='Ci t' />
          <Tool handle={handleUpdateInfo} title='V chng ti' />
          <Tool handle={handleUpdateInfo} title='Bo co li' /> */}
            <Tool handle={handleLogout} title='ng xut' />
          </>
        ) : (
          <div style={{ position: 'relative', height: '100%' }}>
            <div>
              <div className={styles.welcomeTitle}>
                <p>Cho mng bn n vi iSinhVien!</p>
                <button
                  onClick={handleLoginClick}
                  className={styles.buttonOutlined}
                >
                  ng nhp
                </button>
                <button
                  onClick={handleSignUpClick}
                  className={styles.buttonOutlined}
                >
                  ng k ngay
                </button>
              </div>
              <AccountItem route='/support'>H tr</AccountItem>
            </div>
          </div>
        )}
      </div>
    </MainLayout>
  );
}

export default AccountPage;
