import axios from 'axios';
import { useHistory } from 'react-router-dom';
import React, { useState } from 'react';
import authHeader from '../../../utils/authHeader';
import styles from './uploadCard.module.css';
import Compressor from 'compressorjs';

import { useSelector, useDispatch } from 'react-redux';
import { selectUser, updateCard, logoutUser } from '../../../store/userSlice';
import { Loading } from '../../_commons';

function UploadCard() {
  const dispatch = useDispatch();
  const history = useHistory();

  const imageExtensions = ['image/jpeg', 'image/png'];
  const [frontsideName, setFrontsideName] = useState(null);
  const [uploading, setUploading] = useState(null);
  const [uploaded, setUploaded] = useState(null);

  const uploadFrontside = async (e) => {
    setUploading(true);

    if (imageExtensions.includes(e.target.files[0].type)) {
      new Compressor(e.target.files[0], {
        quality: 0.6,
        convertSize: 1000000,
        success(result) {
          const formData = new FormData();
          formData.append('card', result, result.name);

          axios
            .post('/api/user/card', formData, authHeader())
            .then((res) => {
              if (res.status === 200) {
                setUploading(false);
                dispatch(updateCard(res.data.data.card));
                alert('Cp nht thng tin thnh cng!');
                history.go(0);
              }
            })
            .catch((err) => {
              setUploading(false);

              if (err.response?.status === 401) {
                return dispatch(logoutUser());
              }

              alert(
                'Khng th ti ln giy t ty thn. Vui lng lin h zalo: 0797324886  c h tr nhanh nht! ' +
                  err.toString()
              );
            });
        },
        error(err) {
          console.log(err.message);
        },
      });
    } else {
      setFrontsideName('Li: Ch ti ln tp hnh nh JPG v PNG');
      setUploading(false);
    }
  };

  return (
    <div className={styles.container}>
      <div className={styles.formGroup}>
        <label className={styles.formLabel}>Mt trc giy t ty thn</label>
        <p
          style={{
            color: 'var(--primary)',
            fontSize: '0.8rem',
            textAlign: 'center',
          }}
        >
          Nu gp li khi cp nht giy t ty thn, vui lng ng xut ti
          khon v ng nhp li  khc phc.
          <br />
          Vui lng cp nht giy t ty thn trc khi n  khng phi ch i
          lu. Xin cm n.
        </p>
        {uploading ? (
          <Loading message='ang cp nht thng tin...' />
        ) : (
          <label className={styles.formUploadButton}>
            <input
              className={styles.formInput}
              type='file'
              accept='image/*'
              id='frontside'
              name='frontside'
              onChange={uploadFrontside}
              required
            />
            Cp nht
          </label>
        )}
        {frontsideName ? (
          <p className={styles.formFilename}>{frontsideName}</p>
        ) : null}
      </div>
    </div>
  );
}

export default UploadCard;
