import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';

import axios from 'axios';
import { useSelector } from 'react-redux';
import { selectUser } from '../store/userSlice';
import QRCode from 'qrcode.react';

import styles from './couponPage.module.css';
import { MdArrowBack } from 'react-icons/md';
import Loading from '../components/Loading';
import authHeader from '../utils/authHeader';
function CouponPage(props) {
  const serviceType = [
    'Tt c dch v',
    'n ung',
    'Kha hc',
    'In n',
    'ng phc',
  ];
  const history = useHistory();
  const user = useSelector(selectUser);
  const query = new URLSearchParams(props.location.search);
  const couponId = query.get('id');
  const [coupon, setCoupon] = useState(null);
  const [save, setSave] = useState(false);

  useEffect(() => {
    axios
      .get(`/api/coupon/${couponId}`)
      .then((res) => {
        console.log(res.data.data);
        setCoupon(res.data.data);
      })
      .catch((err) => {
        alert('Khng tm thy m gim gi');
      });
  }, [couponId, setCoupon]);

  useEffect(() => {
    if (user.isLoggedIn) {
      axios
        .get(`/api/coupon-user/check?couponId=${couponId}`, authHeader())
        .then((res) => {
          setSave(res.data.data);
        })
        .catch((err) => {
          console.log(err);
        });
    }
  }, [user.isLoggedIn, couponId, setSave]);

  if (!coupon) {
    return <Loading />;
  }

  const today = new Date();
  const notStarted =
    today >= new Date(coupon.startTime) && today < new Date(coupon.expiryTime)
      ? false
      : today < new Date(coupon.startTime)
      ? 'Cha m'
      : 'Ht hn';

  const handleBackClick = () => {
    history.goBack();
  };

  const handleSaveClick = () => {
    axios
      .post('/api/coupon-user/save', { coupon: coupon._id }, authHeader())
      .then((res) => {
        setSave(true);
      })
      .catch((err) => {
        console.log(err);
        alert(
          'Lu m khng thnh cng. Bn khng thuc i tng nhn u i hoc m  c s dng. Xin cm n!'
        );
      });
  };

  return (
    <div className={styles.container}>
      <div className={styles.backButton} onClick={handleBackClick}>
        <MdArrowBack size={28} />
      </div>
      <img src='/logo.jpg' alt='img' />
      <div className={styles.bodyContainer}>
        <h3 className={styles.couponTitle}>{coupon && coupon.name}</h3>
        <span>
          HSD:
          {new Date(coupon && coupon.expiryTime).toLocaleDateString('en-GB')}
        </span>
        <p>p dng: {coupon && serviceType[coupon.serviceType]}</p>

        {!user.isLoggedIn ? (
          <button
            onClick={() => history.push('/login')}
            className={styles.loginButton}
          >
            ng nhp  nhn QR
          </button>
        ) : save && !save.isUsed ? (
          <>
            <p style={{ padding: '0 0.5rem', textAlign: 'center' }}>
              a m ny cho nhn vin  nhn c u i
            </p>

            <QRCode
              id='qrcode'
              value={`https://isinhvien.vn/coupon-scanned?user=${user.data.id}&coupon=${couponId}`}
              size={290}
              level={'H'}
              includeMargin={true}
              style={{
                borderRadius: '5px',
                border: '1px solid rgb(27, 183, 110)',
              }}
            />
          </>
        ) : coupon.requireWhiteList ? (
          <span style={{ textAlign: 'center' }}>
            u i dnh cho mt s ngi dng nht nh.
            <br />
            Xem hng dn nhn u i  m t bn di.
          </span>
        ) : !notStarted ? (
          save && save.isUsed ? (
            ' s dng'
          ) : (
            <button className={styles.saveButton} onClick={handleSaveClick}>
              Lu m  nhn QR
            </button>
          )
        ) : (
          <button className={styles.saveButton} disabled>
            {notStarted}
          </button>
        )}

        <div className={styles.couponDescription}>
          <h3>Chi tit u i</h3>
          <p>
            {coupon && coupon.description
              ? coupon.description
              : 'Cha cp nht'}
          </p>
        </div>
      </div>
    </div>
  );
}

export default CouponPage;
