import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useHistory, useLocation } from 'react-router-dom';

import styles from './guestHouseBillPage.module.css';
import { MdArrowBack } from 'react-icons/md';

import authHeader from '../utils/authHeader';

function GuestHouseBillPage() {
  const { state } = useLocation();
  const history = useHistory();
  const [roomInfo, setRoomInfo] = useState({
    name: '',
    state: 0,
    rentType: 0,
  });

  const price =
    state.rentType === 0 ? roomInfo.dailyPrice : roomInfo.monthlyPrice;
  const totalPrice = price * state.duration;

  useEffect(() => {
    axios
      .get(`/api/guest-house/room/${state.guestHouse}`)
      .then((response) => {
        const result = response.data;

        if (response.status === 200) {
          setRoomInfo(result.data);
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }, []);

  const goBack = () => {
    history.goBack();
  };

  const bookGuestHouse = () => {
    const info = {
      guestHouse: state.guestHouse,
      rentType: state.rentType,
      duration: state.duration,
      startDate: state.startDate,
    };
    axios
      .post('/api/guest-house/book', info, authHeader())
      .then((response) => {
        if (response.status === 200) {
          history.push('/book-guest-house-status', {
            statusText: 'Thnh cng',
          });
        }
      })
      .catch((error) => {
        if (error.response.status === 401) {
          return history.push('/login');
        }

        const result = error.response.data;
        console.log(result);
        if (result) {
          history.push('/book-guest-house-status', {
            statusText: result.message,
          });
        }
      });
  };

  return (
    <div className={styles.container}>
      <div className={styles.titleBar}>
        <button onClick={goBack} className={styles.goBackButton}>
          <MdArrowBack size={25} color='#fff' />
        </button>
        <p className={styles.pageTitle}>Ho n t phng</p>
      </div>
      <div className={styles.roomInfoContainer}>
        <p className={styles.infoTitle}>Thng tin</p>
        <p>Tn phng: {roomInfo.name}</p>
        <p>
          Tnh trng:{' '}
          {roomInfo.state == 0 ? 'Cn phng' : 'Khng th t phng ny'}
        </p>
        <p>
          n gi:{' '}
          {state.rentType == 0 ? roomInfo.dailyPrice : roomInfo.monthlyPrice}{' '}
          vn
        </p>
        <p>
          Hnh thc thu: {state.rentType == 0 ? 'Theo ngy' : 'Theo thng'}
        </p>
        <p>
          Thi gian: {state.duration} {state.rentType == 0 ? 'ngy' : 'thng'}{' '}
          (Bn c th gia hn khi gn ht hn)
        </p>
        <p>
          Ngy bt u:{' '}
          {state.startDate.toLocaleDateString('vi-VN', {
            weekday: 'long',
            year: 'numeric',
            month: 'long',
            day: 'numeric',
          })}
        </p>
      </div>
      <div className={styles.totalPriceContainer}>
        <p>
          Thnh tin: <span>{totalPrice} vn</span>
        </p>
        <button onClick={bookGuestHouse}>t ngay</button>
        {/* <p>Bn c th hu bt c lc no</p> */}
      </div>
    </div>
  );
}

export default GuestHouseBillPage;
