import axios from 'axios';
import { useHistory } from 'react-router-dom';
import React, { useEffect, useState } from 'react';
import { Loading, TitleBar } from '../_commons';
import QrReader from 'modern-react-qr-reader';
import authHeader from '../../utils/authHeader';
import { useSelector, useDispatch } from 'react-redux';
import { selectUser } from '../../store/userSlice';

import styles from './container.module.css';
import { UploadCard, BikeUserInfo } from './components';

function Container(props) {
  const history = useHistory();
  const dispatch = useDispatch();
  const query = new URLSearchParams(props.location.search);
  const id = query.get('id');
  const [data, setData] = useState(null);
  const [scanner, setScanner] = useState(null);
  const [scanned, setScanned] = useState(null);

  const user = useSelector(selectUser);

  useEffect(() => {
    axios
      .get(`/api/bike/${id}`)
      .then((res) => {
        setData(res.data.data);
      })
      .catch((err) => alert('Khng th ti thng tin v xe m bn yu cu.'));
  }, [user.isLoggedIn]);

  if (!data) {
    return <Loading />;
  }

  const handleScanButton = () => {
    setScanner(false);
    setScanner(true);
  };

  const handleError = (error) => {
    console.log(error);
    alert(
      'ng dng khng c quyn truy cp camera. Vui lng cp quyn camera trn trnh duyt ca bn. Hoc lin h: 0797324886  c h tr nhanh nht.'
    );
    history.go(0);
  };

  const onNewScanResult = (data) => {
    if (data) {
      setScanner(false);
      setScanned(true);

      axios
        .post('/api/bike-user', { bike: id, bikeToken: data }, authHeader())
        .then((res) => {
          if (res.status === 200) {
            console.log(res.data.data);
            alert('ng k thu xe thnh cng!');
            history.push('/bicycles');
          }
        })
        .catch((err) => {
          if (err.response.status === 403) {
            return alert(
              'Bn c 1 xe ang thu. Vui lng tr xe trc khi thu li!'
            );
          }

          return alert(
            'M QR  ht hn. Vui lng ti li m mi trc khi qut!'
          );
        });
    }
  };

  return (
    <div className={styles.container}>
      <TitleBar title='Thu xe' navigation='/bicycles' />
      <div className={styles.resultContainer}>
        {user.isLoggedIn ? (
          <>
            <BikeUserInfo />
            <UploadCard />
          </>
        ) : (
          <div className={styles.loginContainer}>
            <p>ng nhp  s dng tnh nng ny</p>
            <button
              onClick={() => history.push('/login')}
              className={styles.scanButton}
            >
              ng nhp
            </button>
          </div>
        )}
      </div>

      <div className={styles.scanContainer}>
        {scanner ? (
          <>
            <QrReader
              delay={100}
              style={{ width: 320, height: 240, borderRadius: '5px' }}
              onError={handleError}
              onScan={onNewScanResult}
              facingMode={'environment'}
              legacyMode={false}
            />
            <p style={{ margin: 0 }}>Ly m QR ti my nhn vin bo v</p>
            <p style={{ margin: 0 }}>
              Nu xy ra li camera, vui lng ti li trang ny hoc lin h
              0797324886  c h tr nhanh nht!
            </p>
          </>
        ) : null}
      </div>
      <div className={styles.infoContainer}>
        <p className={styles.name}>{data && data.name}</p>
        {user.isLoggedIn ? (
          <>
            <button onClick={handleScanButton} className={styles.scanButton}>
              {scanned ? 'Qut li' : 'Qut QR  thu xe ny'}
            </button>
            <p
              style={{
                color: 'var(--primary)',
                fontSize: '0.8rem',
                textAlign: 'center',
              }}
            >
              Qut m QR do bo v cung cp
            </p>
          </>
        ) : null}
        <p className={styles.state}>
          {data && data.isAvailable ? 'ang c sn' : ' c thu'}
        </p>
        <p>Gi gi: {data && data.hourPrice} VN / gi</p>
        <p>Gi ngy: {data && data.dayPrice} VN / ngy</p>
        {data && data.dayPrice === 0 ? (
          <p style={{ textAlign: 'center' }}>
            Min ph trong vng 24h u thu, ph ph 30.000 ng/ngy nu tr
            xe mun hn 24h.
          </p>
        ) : null}

        <img src={data.images[0]} alt='image' />
        <h5 className={styles.descriptionHeader}>Thng tin chi tit</h5>
        <p>{data && data.description}</p>
      </div>
    </div>
  );
}

export default Container;
