import axios from 'axios';
import React, { useEffect, useState } from 'react';
import TitleBar from '../components/TitleBar';
import styles from './photocopyDetailPage.module.css';

import { useDispatch, useSelector } from 'react-redux';
import {
  selectUser,
  updateName,
  updateTel,
  updateZalo,
} from '../store/userSlice';

function PhotocopyDetail(props) {
  const query = new URLSearchParams(props.location.search);
  const id = query.get('id');
  const [data, setData] = useState(null);
  const [documentFile, setDocument] = useState(null);
  const [documentName, setDocumentName] = useState(null);
  const [documentId, setDocumentId] = useState(null);
  const [uploadPercent, setUploadPercent] = useState(null);
  const [uploading, setUploading] = useState(false);
  const [documentSize, setDocumentSize] = useState(null);
  const [receiptFile, setReceiptFile] = useState(null);
  const [receiptName, setReceiptName] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const dispatch = useDispatch();
  const user = useSelector(selectUser);

  useEffect(() => {
    axios
      .get(`/api/photocopy/${id}`)
      .then((res) => {
        if (res.status == 200) {
          setData(res.data.data);
        }
      })
      .catch((err) => alert(err.toString()));
  }, []);

  useEffect(() => {
    if (documentFile) {
      setUploading(true);
      const formData = new FormData();
      formData.append('drive', data.drive);
      formData.append('document', documentFile);

      axios
        .post('/api/photocopy-user/upload', formData, {
          onUploadProgress: (progressEvent) => {
            const percent = parseInt(
              (progressEvent.loaded / progressEvent.total) * 100
            );
            setUploadPercent(percent / 2);

            if (percent == 100) {
              setTimeout(() => {
                setUploadPercent(75);
              }, 1500);
            }
          },
        })
        .then((res) => {
          setDocumentId(res.data.data.documentId);
          setUploadPercent(100);
          setUploading(false);
        })
        .catch((err) => {
          setUploading(false);
          alert(err.toString());
        });
    }
  }, [documentFile]);

  const uploadDocument = (e) => {
    const file = e.target.files[0];
    setDocumentSize(file.size);

    if (file.size > 16e6) {
      return setDocumentName(
        'Khng th ti ln tp c kch thc ln hn 15 MB. Vui lng gi ti liu bng Link hoc gi thnh nhiu ln'
      );
    }

    setDocument(file);
    setDocumentName(file.name);
  };

  const uploadReceipt = (e) => {
    const imageExtensions = [
      'image/jpeg',
      'image/png',
      'image/svg+xml',
      'image/webp',
    ];

    if (imageExtensions.includes(e.target.files[0].type)) {
      setReceiptFile(e.target.files[0]);
      setReceiptName(e.target.files[0].name);
    } else {
      setReceiptName('Li: Tp ti ln khng phi l tp hnh nh');
    }
  };

  const handleNameChange = (e) => {
    dispatch(updateName(e.target.value));
  };

  const handleTelChange = (e) => {
    dispatch(updateTel(e.target.value));
  };

  const handleZaloChange = (e) => {
    dispatch(updateZalo(e.target.value));
  };

  const handleSubmitButton = () => {
    const formNote = document.getElementById('formNote').value;
    console.log(formNote);
    const formAddress = document.getElementById('formAddress').value;
    const formDocumentLink = document.getElementById('formDocumentLink').value;
    console.log(user.name);
    if (!user.data.name) {
      return alert('Vui lng nhp tn ca bn');
    }

    if (!user.data.tel) {
      return alert('Vui lng nhp s in thoi lin h ca bn');
    }

    if (!formNote) {
      return alert('Vui lng nhp hng dn in cho nhn vin');
    }

    if (formDocumentLink || documentId) {
      setIsLoading(true);
      let formData = new FormData();
      formData.append('name', user.data.name);
      formData.append('tel', user.data.tel);
      formData.append('zalo', user.data.zalo || '');
      formData.append('address', formAddress);
      formData.append('note', formNote);
      formData.append('photocopyInfo', id);
      formData.append('documentId', documentId);
      formData.append('receipt', receiptFile);
      formData.append('drive', data.drive);

      if (documentId) {
        const documentLink = `https://drive.google.com/file/d/${documentId}`;
        formData.append('documentLink', documentLink);
      } else {
        formData.append('documentLink', formDocumentLink);
      }

      axios
        .post('/api/photocopy-user/add', formData)
        .then((res) => {
          setIsLoading(false);
          alert('Bn  gi ti liu thnh cng!');
        })
        .catch((err) => {
          alert(
            'Li: ' +
              err.toString() +
              '. Lin h admin ti zalo: 0797324886  c h tr nhanh nht. Xin cm n!'
          );
          setIsLoading(false);
        });
    } else {
      alert('Vui lng ti ln tp hoc nh km lin kt ti liu!');
    }
  };

  return (
    <div>
      <TitleBar title='Thng tin in n' navigation='/photocopies' />

      <form className={styles.formContainer}>
        <div className={styles.uploadContainer}>
          <div className={styles.formGroup}>
            <label
              className={styles.formUploadButton}
              style={{ margin: '0 auto' }}
            >
              <input
                className={styles.formInput}
                type='file'
                id='document'
                name='document'
                onChange={uploadDocument}
              />
              <img src='/uploadicon.png' style={{ width: '3rem' }} />
              <span>Ti tp ln</span>
            </label>
            {uploading ? (
              <p style={{ textAlign: 'center' }}>
                ang ti tp ln... {uploadPercent}%
              </p>
            ) : null}
            {documentName ? (
              <>
                <p className={styles.formFilename}>{documentName}</p>
                <button
                  type='button'
                  className={styles.button}
                  onClick={() => {
                    setDocumentId(null);
                    setDocumentName(null);
                  }}
                >
                  Xa tp
                </button>
              </>
            ) : null}
          </div>

          <div
            className={styles.formGroup}
            style={{ alignItems: 'center', margin: '1rem 0', width: '100%' }}
          >
            <label for='formDocumentLink'>Hoc</label>
            <input
              id='formDocumentLink'
              type='text'
              placeholder='Nhp ng dn lin kt n ti liu'
              style={{
                width: '100%',
                textAlign: 'center',
                margin: '1rem 0',
                padding: '0.5rem',
              }}
            />
            <div>
              <button
                type='button'
                className={styles.button}
                onClick={() => {
                  document.getElementById('formDocumentLink').value = null;
                }}
              >
                Xa lin kt
              </button>
            </div>
          </div>
        </div>
        <div className={styles.infoContainer}>
          <p style={{ margin: 0, color: ' #ff0000 ' }}>* bt buc</p>
          <div className={styles.formGroup}>
            <label for='formName' className={styles.formLabel}>
              Tn ca bn*
            </label>
            <input
              className={styles.formInput}
              value={user.data.name}
              id='formName'
              type='text'
              placeholder='Nhp y  h tn, c du'
              onChange={handleNameChange}
            />
          </div>

          <div className={styles.formGroup}>
            <label for='formTel' className={styles.formLabel}>
              in thoi lin h*
            </label>
            <input
              className={styles.formInput}
              value={user.data.tel}
              id='formDocument'
              type='text'
              placeholder='Nhp s in thoi lin h ca bn'
              onChange={handleTelChange}
            />
          </div>
          <div className={styles.formGroup}>
            <label for='formZalo' className={styles.formLabel}>
              S in thoi Zalo (Khng bt buc)
            </label>
            <input
              className={styles.formInput}
              value={user.data.zalo}
              id='formZalo'
              type='text'
              placeholder='Nhp s in thoi zalo ca bn'
              onChange={handleZaloChange}
            />
          </div>
          <div className={styles.formGroup}>
            <label for='formAddress' className={styles.formLabel}>
              a ch giao hng
            </label>
            <input
              className={styles.formInput}
              value={user.data.address}
              id='formAddress'
              type='text'
              placeholder='B trng nu ly ti ca hng'
            />
          </div>
          <div className={styles.formGroup}>
            <label for='formNote' className={styles.formLabel}>
              Hng dn in*
            </label>
            <textarea
              className={styles.formInput}
              id='formNote'
              rows='5'
              placeholder='Nhp hng dn in cho nhn vin: s trang cn in, loi giy in, kh in,... Bn c
            cn xc nhn trc khi in khng?'
            />
          </div>
          <div className={styles.formGroup}>
            <label className={styles.formLabel}>
              Bin lai (nu  chuyn khon, thng tin chuyn khon  phn bn
              di)
            </label>
            <label className={styles.formUploadButton}>
              <input
                className={styles.formInput}
                type='file'
                id='formReceipt'
                name='receipt'
                onChange={uploadReceipt}
              />
              Ti nh ln
            </label>

            {receiptName ? <p>{receiptName}</p> : null}
          </div>

          {isLoading ? (
            <>
              <p>
                H thng ang x l, vui lng ch trong t nht 15 giy {'<3'}
              </p>
              <p className={styles.formSubmitButton}>ang ng k...</p>
            </>
          ) : (
            <button
              type='button'
              onClick={handleSubmitButton}
              className={styles.formSubmitButton}
            >
              Gi ti liu ngay
            </button>
          )}
        </div>
      </form>

      <div className={styles.photocopyContainer}>
        <h3>Thng tin im in n</h3>
        <p className={styles.name}>{data && data.name}</p>
        <p>a ch: {data && data.address}</p>
        <p>S in thoi: {data && data.tel}</p>
        <p>
          Zalo ca hng:{' '}
          {data && (
            <a
              href={`https://zalo.me/${data.zalo}`}
              target='_blank'
              rel='noopener noreferrer'
            >
              {data.zalo}
            </a>
          )}
        </p>
        <img src={data && data.images[0]} />
        <p style={{ textAlign: 'center', fontWeight: 'bold' }}>
          Thng tin chi tit
        </p>
        <p>{data && data.description}</p>
      </div>
    </div>
  );
}

export default PhotocopyDetail;
