import React, { useEffect, useState } from 'react';
import TitleBar from '../components/TitleBar';
import axios from 'axios';
import { useHistory } from 'react-router-dom';

import styles from './jobPage.module.css';
import Loading from '../components/Loading';

function JobPage() {
  const history = useHistory();
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    axios
      .get('/api/job')
      .then((res) => {
        setData(res.data.data);
        setLoading(false);
      })
      .catch((error) => {
        console.log(error);
        setLoading(false);
        alert(error);
      });
  }, []);

  return (
    <div className={styles.container}>
      <TitleBar title='Vic lm sinh vin' />
      {loading ? <Loading /> : null}
      {data.map((child) => {
        let date = new Date(child.createdAt);
        date = `${date.getDate()}/${date.getMonth()}/${date.getFullYear()}`;
        let state = 'Khng c';
        if (child.state === 1) {
          state = 'ang m';
        } else if (child.state === 2) {
          state = ' ng';
        }
        return (
          <div className={styles.jobContainer} key={child._id}>
            <div className={styles.imageContainer}>
              <img alt='image' src={child.banner} />
            </div>
            <div className={styles.infoContainer}>
              <p className={styles.jobTitle}>{child.title}</p>
              <p>Ngy ng: {date}</p>
              <p>Thi gian: {child.time}</p>
              <p
                className={styles.jobState}
                style={
                  child.state == 2
                    ? {
                        margin: '0.5rem 0',
                        backgroundColor: 'rgb(245, 55, 55)',
                      }
                    : { margin: '0.5rem 0' }
                }
              >
                {state}
              </p>
              <div className={styles.buttonContainer}>
                <button
                  className={styles.contactButton}
                  onClick={() => history.push(`/job?id=${child._id}`)}
                >
                  Xem thng tin lin h
                </button>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
}

export default JobPage;
